* {
  font-family: Helvetica, Arial, sans-serif;
}
body {
  padding: 60px;
}

input[type="text"] {
  color: grey;
  width: 100%;
  font-size: 1.2rem;
}

.card-grid {
  display: flex;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin: 0px 10px 10px 0px;
  /* overflow: hidden; */
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  padding: 10px;
  display: block;
  margin: auto;
}
